.Header {
  display: flex;
  justify-content: space-between;
  text-align: center;
  box-shadow: 0 3px 5px #e3e3e3;
  padding: 0.5em;
  /* margin-bottom: 1em; */
}

.Header .settings {
  width: 2.2em;
  height: 2.2em;
  cursor: pointer;
  transition: 0.3s;
}

.Header .settings:hover {
  transform: rotate(60deg);
}

.Header .rule-types > button {
  padding: 0.3em 0.5em;
  border: none;
  font-size: 18px;
  cursor: pointer;
  transition: 0.1s;
}

.Header .rule-types > button:focus {
  outline: none;
}

.Header .rule-types > button:first-child {
  border-radius: 5px 0 0 5px;
}

.Header .rule-types > button:last-child {
  border-radius: 0 5px 5px 0;
}

.rule-types .selected {
  color: #eeeeee;
  background: #1c1917;
}

.rule-types .unselected {
  color: #1c1917;
  background: #eeeeee;
}
