.Footer {
  width: 80%;
  margin: auto;
}

.footer-components {
  display: flex;
}

.operations {
  display: flex;
  justify-content: center;
  margin-top: 1em;
}

.operations button {
  margin: 0 0.5em;
  padding: 0.5em 1em;
  font-size: 20px;
  border-radius: 5px;
  transition: 0.2s;
  cursor: pointer;
}

.operations button:focus {
  outline: none;
}

.operations button:hover {
  transform: scale(1.05, 1.05);
}

.save-button {
  color: #eeeeee;
  background: #1c1917;
}

.save-button:active {
  color: white;
  background: black;
}

.clear-button {
  border: solid 1px black;
  background: #eeeeee;
}

.clear-button:active {
  background: white;
}
