.Body {
  display: flex;
  justify-content: center;
  width: 80%;
  max-height: 50vh;
  margin: auto;
  margin-top: 1.5em;
  /* overflow-y: scroll;
  overflow-x: hidden; */
}

.left {
  width: 60%;
  display: flex;
  flex-direction: column;
  margin-right: 1.5em;
}

.left-displays {
  display: flex;
  justify-content: space-between;
}

.right {
  width: 40%;
  display: flex;
  flex-direction: column;
  margin-bottom: 2em;
}

.input-field {
  display: flex;
  flex-direction: column;
  margin-bottom: 0.5em;
  font-size: 20px;
}

/*
  All inputs and their labels
*/

input,
textarea {
  padding: 0.35em;
  font-size: 20px;
  border-radius: 5px;
  border: solid 2px rgb(200, 200, 200);
  transition: 0.1s;
}

input:focus,
textarea:focus {
  outline: none;
  border: solid 2px rgb(120, 120, 120);
}

label {
  font-weight: 600;
}

.display-header {
  display: flex;
  align-items: baseline;
}

.subnote {
  margin: 0;
  font-size: 12px;
  overflow-x: scroll;
  height: 1.5em;
}
