.Settings {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background: rgba(100, 100, 100, 0.5);
  z-index: 5;
}

.Settings > .center-piece {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 400px;
  margin-top: -5em;
  padding: 1em;
  background: white;
  border-radius: 3px;
  box-shadow: rgb(99, 99, 99) 0 0 5px;
}

.Settings .header {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
}

.Settings .header p {
  margin: 0.5em 0;
}

.Settings .close-settings {
  cursor: pointer;
}

.Settings h3 {
  margin: 0.5em 0;
}

.Settings label {
  margin: 0.5em 0;
}

.Settings .password {
  display: flex;
  flex-direction: column;
}

.Settings button {
  max-width: 5em;
  margin: 0.5em 0;
  padding: 0.5em 0;
  font-size: 16px;
  border-radius: 5px;
  transition: 0.2s;
  cursor: pointer;
}

.Settings .password button {
  color: #eeeeee;
  background: #1c1917;
}

.Settings button:focus {
  outline: none;
}

.Settings .subnote {
  max-width: 350px;
  height: auto;
  margin: 0.5em 0;
  color: rgb(255, 50, 0);
}

.Settings .logout-btn {
  padding: 0.5em;
  margin: auto;
}
