.Notification {
  position: absolute;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  background: rgba(100, 100, 100, 0);
}

.Notification > .center-piece {
  background: white;
  box-shadow: 0 0 8px #aaa;
  display: flex;
  align-items: center;
  /* justify-content: center; */
  /* height: 3em; */
  padding: 0 1em;
  margin-right: 2em;
  margin-bottom: 2em;
  z-index: 6;
}
