.selector-input {
  display: flex;
  margin: 0.5em 0;
}

.selector-input-token {
  width: -moz-calc(100% - 0.75em);
  width: -webkit-calc(100% - 0.75em);
  width: -o-calc(100% - 0.75em);
  width: calc(100% - 0.75em);
}

.selector-input-regex,
.selector-input-token-type {
  width: 100%;
}

.selector-input-regex {
  margin-right: 0.2em;
}

.selector-input-token-type {
  margin-left: 0.2em;
}

.selector-label {
  margin-right: 0.5em;
}
