.HtmlDisplay {
  width: 40%;
  background: #ffffff;
  border: solid 2px rgb(120, 120, 120, 0.5);
  border-left: none;
  border-radius: 2px;
}

.display {
  overflow-y: auto;
  padding: 1.5em 1.5em;
  font-family: "Fira Sans", sans-serif;
  text-align: left;
  max-height: 20vh;
}

.display h1:first-child {
  margin-top: 0;
}

.display p {
  margin-left: 0;
}

code {
  padding: 0em 1em;
  border-radius: 5px;
  overflow-x: auto;
  font-size: 14px;
}
