.RuleList {
  height: 100%;
  width: 100%;
  margin-bottom: 0.5em;
  background: #ffffff;
  overflow-y: scroll;
  font-size: 18px;
  border: solid 2px rgb(200, 200, 200);
  border-radius: 5px;
}

.RuleList:focus {
  outline: none;
}

.RuleList > div {
  padding: 0.5em;
  cursor: pointer;
}

.RuleList > .selected {
  color: #fcffe0;
  background: #38403d;
}

.RuleList > .unselected,
.RuleList > .selected {
  display: flex;
  justify-content: space-between;
}

.RuleList > .remove-rule:active {
  color: blue;
}
